@media screen and (min-device-aspect-ratio: 3072/4096) {
  #header {
    display: flex !important;
  }
  #back-header {
    display: none !important;
  }
  #main {
    padding-top: 68px;
    background-image: url("~@/assets/cloudtop.svg");
    background-attachment: fixed;
    background-size: contain;
    background-position: top center;
    background-color: #cae9f7;
    height: 100%;
    background-repeat: no-repeat;
  }
  #cloud-body {
    background: transparent !important;
    padding: 0 70px 30px 70px !important;
  }
  #content {
    max-width: 95%;
    margin: 0 auto;
    margin-top: 50px;
    background: #fff;
    padding-top: 47px;
    position: relative;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  }
  #content-header {
    position: absolute;
    background: #00a9ce;
    color: #fff;
    left: 0;
    right: 0;
    top: 0;
    height: 48px;
    display: -ms-flexbox;
    display: flex;
    padding-left: 15px;
    -ms-flex-direction: row;
    flex-direction: row;
  }
  #content-header #back {
    font-size: 1.2rem;
    padding: 14px 10px 15px 0px;
  }
  #content-header #fc {
    flex: 1;
  }
  #content-header a {
    color: #fff;
  }
  #content-header #logo {
    padding: 10px 5px;
    line-height: 30px;
    font-size: 1.2rem;
    letter-spacing: 0.1rem;
  }
}
@media screen and (max-device-aspect-ratio: 3071/4096) {
  #main {
    height: 100%;
    padding-top: 68px;
    padding-bottom: 30px;
    background: #fff;
    background-repeat: no-repeat;
    background-position: top center;
    background-size: contain;
  }
  #content-header {
    display: none !important;
  }
  #info-footer {
    display: block !important;
  }
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #92c020;
}
video,
audio {
  max-width: 100%;
}
#back-header {
  position: fixed;
  background: #00a9ce;
  color: #fff;
  left: 0;
  right: 0;
  top: 0;
  height: 54px;
  padding-top: 4px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  z-index: 101;
}
#back-header #back {
  font-size: 1.2rem;
  padding: 16px 10px 15px 20px;
}
#back-header #fc {
  flex: 1;
}
#back-header a {
  color: #fff;
}
#back-header #logo {
  padding: 10px 5px;
  line-height: 32px;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
}
#tabs {
  display: flex;
