@page {
  size: A4;
  margin: 0;
}
@media print {
  html,
  body {
    width: 210mm;
    height: 297mm;
  }
  #header,
  #footer,
  #info-footer,
  #tabs,
  #logo,
  #filters {
    display: none !important;
  }
  #session .date-created {
    font-size: 1.2rem !important;
    color: #777 !important;
    padding: 0 10mm 5mm 10mm;
  }
  .pagebreak {
    page-break-before: always;
    clear: both;
  }
  .chart-container {
    width: 176mm;
  }
  .chart-container canvas {
    min-height: 100%;
    max-width: 100%;
    max-height: 500px;
    height: auto !important;
    width: auto !important;
  }
  .chart-container #overall-ratings canvas {
    max-height: 500px;
  }
}
