.heading {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
  box-sizing: border-box;
}
h3,
h4 {
  flex: 1;
  text-align: left;
}
#edit-event {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
#edit {
  padding: 10px 15px;
  background: #92c020;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  margin: 2px;
}
#event {
  padding: 5px 10px;
}
#event router-link {
  width: 100%;
  text-align: center;
  padding: 15px 30px;
  border-radius: 10px;
  display: block;
/* background: #E7EFEE; */
  margin: 2px;
  text-decoration: none;
  font-size: 1.1rem;
}
#event router-link#current {
  background: #00a9ce;
  color: #fff;
}
#event a {
  text-align: center;
  padding: 15px 30px;
  display: block;
/* background: #E7EFEE; */
  margin: 2px;
  text-decoration: none;
  font-size: 1.1rem;
}
#event a#current {
  background: #00a9ce;
  color: #fff;
}
