h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #92c020;
}
video {
  max-width: 100%;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}
#answer-buttons {
  padding: 20px;
  padding-top: 30px;
}
#answer-buttons .row {
  display: flex;
  text-align: center;
  padding: 1px;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-direction: row;
  flex-direction: row;
}
#answer-buttons .row #title {
  text-align: center;
  width: 100%;
  font-size: 1.6rem;
  text-transform: uppercase;
/* font-weight: bold; */
  color: #bbd37a;
  letter-spacing: 0.05em;
}
#answer-buttons .row #subtitle {
  text-align: center;
  width: 100%;
  font-size: 1.2rem;
  font-weight: lighter;
  text-transform: lowercase;
  letter-spacing: 0.1rem;
}
#answer-buttons .row input {
  margin: 20px auto 10px auto;
  padding: 10px 1px;
  border: none;
  border-bottom: 1px solid #92c020;
  font-size: 1rem;
  color: #444;
  width: 90%;
}
#answer-buttons .row button {
  font-size: 12pt;
  margin: 0 auto;
  border: none;
  padding: 10px 30px;
  background-color: #fff;
  background: #00a9ce;
  color: #fff;
/* text-transform: uppercase; */
  font-weight: bold;
  font-size: 1rem;
}
#answer-buttons .row a {
  display: block;
  text-decoration: none;
  padding: 30px 20px 15px 20px;
  flex: 0.5;
  font-size: 5rem;
  margin: 5px;
}
