













































































































































































































































































































@import '~stylus/shared'

#date-created
    font-size: 0.85rem;
    color: $text-lt-gray;
    margin-bottom 5px;
.ev-name
  display: flex
  flex-direction: row
  justify-content space-between
  align-items center
  width 100%

.session-edit
  color: $secondary
  margin-left: 10px
  text-decoration: none
  cursor: pointer
  width: auto !important

.heading
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  h3, h4
    flex: 1
    text-align: left
.optional
  font-size: 0.8 em
  color: $text-gray
  margin 0 5px
#edit-event
  margin 0 auto
  display: flex
  flex-direction row
  justify-content center
#edit
  // border-radius 10px
  padding 10px 15px
  background $secondary
  color $text-light
  font-weight: bold
  cursor pointer
  margin: 2px

#discard
  // border-radius 10px
  padding 10px 15px
  background $button-gray
  color $text-light
  font-weight: bold
  cursor pointer
  margin: 2px

#save
  // border-radius 10px
  padding 10px 15px
  background $button-primary
  color $text-light
  font-weight: bold
  cursor pointer
  display: inline-block
  margin: 2px

#event-settings
  text-align: left

  .section
    padding 10px
    border 1px solid $border-light
    margin-bottom: 10px
  h4
    text-align center
    margin-bottom 0px
.row
  padding 10px 0
label
  color $secondary
  font-size 0.9rem
  font-weight: bold
  display: block
.label-for
  display: inline-block !important
.event-data
  padding 5px 10px
  font-size: 1rem
  display: block
  width 100%

.no-data
  font-weight: bold
  font-style italic
  font-size 0.9rem
  color $button-gray

input[type=text].event-input
  padding 10px
  font-size 0.9rem
  min-width: 50%
  border 1px solid $button-gray
  width: 100%
  box-sizing: border-box;

select.event-input
  padding 10px
  font-size 0.9rem
  min-width: 50%

#add-session
  text-align: left

#session
  // background: #fafafa;
  border: 1px solid #eee;
  padding: 10px;
  // border-radius: 10px;
  margin-bottom 10px
  margin-top 10px

.event
  display: flex
  flex-direction row
  justify-content space-between
  align-items center
  padding 10px 10px
  margin-bottom: 10px
  background: #fafafa;
  border: 1px solid $border-light;
  // border-radius: 10px;
  &:active
    background: $button-gray
  .event-data
    text-align: left;
  .ev-name
    font-size: 1.1rem;
    color: $secondary;
    font-weight: bold;
    margin-bottom: 5px;
  .ev-date
    font-size: 0.85rem;
    color: $text-lt-gray;
    margin-top: 5px;
  a
    width: 100%
    padding 5px
    text-align center

.quill-data >>> img
  max-width: 100%
