#answer-buttons {
  padding: 20px;
}
#answer-buttons .row {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
#answer-buttons .row a {
  display: block;
  text-decoration: none;
  padding: 30px 20px 15px 20px;
  flex: 0.5;
  font-size: 5rem;
  margin: 5px;
  border-radius: 10px;
}
#privacy {
  text-align: left;
  padding: 20px;
}
#privacy .bold {
  font-weight: 700;
}
#privacy ul {
  padding: 0 0 0 20px;
  list-style-type: disc !important;
}
#privacy ul li {
  margin: 10px 0;
  display: list-item;
}
