














































































































































































































































































































































































































































@import '~stylus/shared'

@media screen and (min-device-aspect-ratio: 3072/4096)
  #header-container
    display block !important
  #main
    padding-top: 68px
  #video-controls
    bottom: 70px !important
    position: fixed !important

#header-container
  display none

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: $primary;
}

#main {
  padding-bottom: 74px;
}

video#record {
  max-width: 100vw;
  width: 100vw;
  max-height: calc(100vh - 54px);
}

#playback
  max-width: 100vw;
  max-height: 60vh
  // z-index: -100
#record
  transform: scaleX(-1)

#recording
  background: black

#video-picker
  position absolute
  top: -200px

#error
  font-size: 1.6em;
  padding: 50px 20px;
  color: #777
  font-weight: 500

  #skip
    display: block;
    margin: 10px auto;
    margin-top: 30px;
    font-size: 1.2rem;
    padding: 15px 30px;
    border-radius: 10px;
    background: $button-primary;
    color: white;
    border: none;
    width: 300px;

#playback-controls
  padding: 10px 20px;
  background: white

  #consent-container
    width: 100%
    max-width: 600px
    margin 0 auto
    label
      margin 10px auto
      display block
      text-align left
      span
        margin-left: 10px

  #pb-container
    display: flex
    flex-direction: row
    flex-wrap: wrap
    &>button
      min-width 150px
      margin: 0 5px 10px 5px

  #upload
    display: block;
    margin: 10px auto;
    font-size: 1.2rem;
    padding: 15px 30px;
    // border-radius: 10px;
    background: $button-primary;
    font-weight 500
    color: $text-light;
    width: 100%;
    border: none;
    flex: 1;

    &:disabled
      background #777
      color #555

  #restart
    display: block;
    margin: 10px auto;
    font-size: 1.2rem;
    padding: 15px 30px;
    // border-radius: 10px;
    background: $button-lt-gray;
    color: $text-dark;
    font-weight 500
    width: 100%;
    border: none;
    flex: 1;
  #skip
    width: 100%

#none
  padding: 10px 30px;
  background: $button-lt-gray;
  border: 1px solid $border-light;
  margin: 10px 5px;
  // border-radius: 10px;
  text-align: center;
  color: $button-primary;
  box-sizing: border-box

  a
    color: $button-primary;
    font-size: 1.3rem;
    display: block

#video-controls
    position: absolute;
    bottom: 55px;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    padding: 10px 40px;
    align-items: center;
    text-align: center;
    justify-content: center;

  #vc-container
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    max-width: 500px;
    flex: 1;
    align-items: center;

  .vc
    flex: 0.3;

    button
      outline:none

    #skip
      padding: 10px 20px;
      font-size: 1.1rem;
      background: white;
      border: 2px solid #eee;
      border-radius: 50px;
      color: #666;
      font-weight: bold;

    #recording-btn
      width: 70px;
      height: 70px;
      border-radius: 100px;
      background: crimson;
      border: 2px solid white;
      color: white;
      text-align: center;
      font-size: 1.6rem;
      &.recording
        background: white;
        color: crimson;
      &:active
        background: darkred;
        color: white

    #recording-btn-container
      width: 70px;
      height: 70px;
      border-radius: 150px;
      background: white;
      padding: 3px;
      margin: auto;
      border: 3px solid #eee;

    #flip-camera
      width: 50px;
      height: 50px;
      border-radius: 100px;
      font-size: 1.1rem;
      color: #666;
      background: white;
      border: 2px solid #eee;

#answer-buttons {
  padding: 20px;

  .row {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    a {
      display: block;
      text-decoration: none;
      padding: 30px 20px 15px 20px;
      flex: 0.5;
      font-size: 5rem;
      margin: 5px;
      border-radius: 10px;
    }
  }
}
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  margin: 10px 0 !important;
}

/* Hide default HTML checkbox */
.switch input {display:none;}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #f44336;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px;
  margin: 0 !important;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #4caf50;
}

input:focus + .slider {
  box-shadow: 0 0 1px #4caf50;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.toggle-row {
  text-align: left;
  margin 20px 0
}

.toggle-element {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  .answer.public {
    margin: 5px;
    min-width: 50px;
    text-align: center;
    font-size: 1rem;
    font-weight: bold
    color: #778594
  }

  .answer.public.selected {
    color: #2c3e50
  }
}
