@media screen and (min-device-aspect-ratio: 3072/4096) {
  #fc {
    display: none !important;
  }
  .info-footer {
    display: flex !important;
  }
}
#footer {
  position: fixed;
  background: #92c020;
  border-top: 6px solid #bbd37a;
  color: #fff;
  left: 0;
  right: 0;
  bottom: 0;
  height: 50px;
  display: flex;
  padding-top: 10px;
  flex-direction: row;
  justify-content: center;
}
#footer #fc {
  flex: 1;
  display: block;
}
#footer a {
  color: #fff;
  font-size: 1.6rem;
}
#footer .info-footer {
  font-size: 0.9rem;
  margin: 5px 0 10px 0;
  display: none;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
#footer .info-footer span {
  margin: 0 10px;
}
#footer .info-footer a {
  font-size: 0.9rem;
  font-weight: 500;
}
#footer .info-footer a:hover {
  color: #444;
}
#footer .info-footer .idi {
  height: 40px;
  margin-right: 50px;
  position: absolute;
  top: 10px;
  left: 25px;
}
#footer #currently-logged {
  font-size: 0.75rem;
  margin-top: 5px;
  font-weight: 500;
  font-style: italic;
}
#footer #currently-logged span {
  font-weight: bold;
  margin: 0 5px;
}
