#main {
  height: 100%;
  background-repeat: no-repeat;
  background-position: top center;
  background-size: contain;
}
#cloud-head {
  position: relative;
  padding-top: 70px;
  background: #4192a1;
}
#cloud-head img {
  width: 100%;
  margin-bottom: -20px;
}
#cloud-head #head-content {
  position: absolute;
  bottom: 15px;
  right: 0;
  color: #00a9ce;
  font-size: 2.2rem;
  left: 0;
  text-align: center;
  letter-spacing: 0.1rem;
}
#cloud-body {
  background: #fff;
  padding: 0 20px;
}
#answer-buttons {
  padding: 10px 20px;
}
#events-view {
  padding: 10px 20px;
}
#events-view .button {
  display: block;
  margin: 10px auto;
  font-size: 1.2rem;
  padding: 15px 30px;
  background: #92c020;
  color: #fff;
  border: none;
  font-weight: 500;
}
#events-view #signout {
  display: block;
  margin: 10px auto;
  font-size: 1.2rem;
  padding: 15px 30px;
  background: #e7efee;
  color: #444;
  font-weight: 500;
  border: none;
}
