.event {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px 10px;
  margin-bottom: 10px;
  background: #fafafa;
  border: 1px solid #eee;
}
.event:active {
  background: #eee;
}
.event .event-data {
  text-align: left;
}
.event .ev-name {
  font-size: 1.1rem;
  color: #00a9ce;
  font-weight: bold;
  margin-bottom: 5px;
}
.event .ev-date {
  font-size: 0.85rem;
  color: #8899a9;
  margin-top: 5px;
}
.event .event-icons {
  display: flex;
  flex-direction: row-reverse;
}
.event .ev-rating {
  margin: 0 0 0 10px;
  padding: 5px 10px;
  color: #fff;
  background: #f4f4f4;
  border-radius: 10px;
  display: flex;
  font-size: 0.95rem;
  flex-direction: column;
  justify-content: center;
  min-height: 35px;
  width: 25px;
  font-weight: bold;
}
.event .ev-media {
  margin: 0 0 0 10px;
  padding: 10px 10px;
  background: #f4f4f4;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size: 20px;
  width: 25px;
  height: 25px;
  color: #8899a9;
}
.event a {
  width: 100%;
  padding: 5px;
  text-align: center;
}
#header {
  position: fixed;
  background: #00a9ce;
  color: #fff;
  left: 0;
  right: 0;
  top: 0;
  height: 54px;
  padding-top: 4px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  z-index: 101;
}
#header #back {
  font-size: 1.2rem;
  padding: 16px 10px 15px 20px;
}
#header #fc {
  flex: 1;
}
#header a {
  color: #fff;
}
#header #logo {
  padding: 10px 5px;
  line-height: 32px;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
}
