







































































@import '~stylus/shared'

@media screen and (min-device-aspect-ratio: 3072/4096)
  #fc
    display none !important
  .info-footer
    display flex !important
//   #footer
//     display none !important

#footer
  position: fixed
  background: $primary
  border-top: 6px solid $accent
  color: $text-light
  left: 0
  right: 0
  bottom: 0
  height: 50px
  display: flex
  padding-top: 10px
  flex-direction: row;
  justify-content: center;

  #fc
    flex: 1
    display block
  a
    color: $text-light
    font-size: 1.6rem

  .info-footer
    font-size 0.9rem
    margin 5px 0 10px 0;
    display none
    flex-direction row
    justify-content center
    align-items center
    span
      margin 0 10px
    a
      font-size 0.9rem
      font-weight 500
    a:hover
      color $text-gray
    .idi
      height: 40px;
      margin-right: 50px
      position: absolute
      // margin-top:20px
      top: 10px;
      left: 25px

  #currently-logged
    font-size: 0.75rem;
    margin-top: 5px;
    font-weight: 500
    font-style italic

    span
      font-weight: bold
      margin: 0 5px;

