.heading {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
h3,
h4 {
  flex: 1;
  text-align: left;
}
.optional {
  font-size: 0.8em;
  color: #444;
  margin: 0 5px;
}
#edit-event {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
#discard {
  padding: 10px 15px;
  background: #bbb;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  margin: 2px;
}
#save {
  padding: 10px 15px;
  background: #92c020;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  display: inline-block;
  margin: 2px;
}
#event-settings {
  text-align: left;
  padding: 10px 30px;
}
#event-settings .section {
  padding: 10px;
  border: 1px solid #eee;
  margin-bottom: 10px;
}
#event-settings h4 {
  text-align: center;
  margin-bottom: 0px;
}
.row {
  padding: 10px 0;
}
label {
  color: #00a9ce;
  font-size: 0.9rem;
  font-weight: bold;
  display: block;
}
.label-for {
  display: inline-block !important;
}
input[type=text].event-input {
  padding: 10px;
  font-size: 0.9rem;
  min-width: 50%;
  border: 1px solid #ddd;
  width: 100%;
  box-sizing: border-box;
}
select.event-input {
  padding: 10px;
  font-size: 0.9rem;
  min-width: 50%;
}
#add-session {
  text-align: left;
}
