
















































































































































































































































































































@import '~stylus/shared'

#question
  font-size: 1.2rem;
  white-space: pre-wrap;

#question >>> li
  display: block !important;

.date-created
  font-size: 0.85rem;
  color: $text-lt-gray;
  margin-bottom 5px;
  font-weight: bold;

#session
  // background: #fafafa;
  border: 1px solid $border-light;
  // border-radius: 10px;
  padding: 10px 10px;
  margin-bottom 10px
  margin-top 10px
  background darken($body-background, 3)

.ev-rating
  margin: 0 0 0 10px;
  padding: 5px 10px;
  color: $text-light;
  background: $button-lt-gray;
  // border: 1px solid #ddd;
  border-radius: 5px;
  display flex;
  font-size: 0.95rem;
  // font-size: 12px;
  flex-direction: column
  justify-content center
  min-height 25px
  font-weight bold;
  min-width: 25px;

img.ev-rating
  width: 25px;
  height: 25px;
  padding: 5px 5px;

#feedback
  background: #fdfdfd;
  border: 1px solid $border-light;
  padding: 10px;
  // border-radius: 10px;
  margin-bottom 30px

#feedback-data
  display flex
  flex-direction row
  justify-content space-between
  align-items center
  margin 10px auto

  #date
    font-size: 0.85rem;
    color: $text-lt-gray;
    margin-bottom: 5px;
    text-align: left;
  #rating
    margin: 0 0 0 10px;
    padding: 10px 10px;
    // background: #f4f4f4;
    // border-radius: 10px;
    // display block;
    // width: 25px;
    // height: 25px;
    display: flex
    flex-direction: row-reverse

  #feedback-media
    margin-top: 10px

video, audio
  max-width 100%
.text-feedback.media
  font-weight: 500;
  font-size: 1.1rem;
  white-space: pre-wrap;
  padding: 30px;
  border-radius: 5px;
  border: 1px solid darken($body-background, 10);
  margin: 0 auto;
  text-align: left

#filters
  display: flex
  border 1px solid $border-light
  // padding: 10px;
  flex-direction: row
  flex-wrap: wrap
  justify-content center
  align-items: center
  background darken($body-background, 3)
.filter-row
  // padding: 10px;
  // border 1px solid $border-light
  border: 0.25rem solid darken($body-background, 3)
  border-collapse collapse
  min-height: 2rem;
  display: flex
  flex-direction: row
  align-items: center
  flex-wrap: wrap
  margin: 10px

  select
    padding: 10px;

.filter
  margin 0 10px
  display: flex
  flex-direction: row
  label
    font-size: 0.85rem
    line-height: 2rem
    margin-right: 5px
    font-weight: bold
  input, select
    font-size: 0.8rem
    font-weight: bold;
    padding 5px 0.5rem
    border 1px solid $secondary
    color: $secondary
    background: white

#session-filter
  flex-grow: 1
  .filter
    flex-wrap: wrap
  label, select
    flex: 1

#feedback-type-filter, #rating-filter
  justify-content flex-start
  margin-left: 0
  margin-right: 0
  border: none

  .filter
    flex-wrap: wrap
    margin-left: 0
    margin-right: 0
  label, select
    margin: 0 5px
  select
    min-width: 100px;

#time-period-filter
  justify-content: center
  flex-wrap: nowrap
  flex-direction: row-reverse
  .filter
    margin 0 5px
  .time-period
    font-size: 0.8rem
    font-weight: bold
    color: $text-gray
    border: none
    background: none
    padding: 5px 10px
    border-radius: 5px
    min-height: 32px
    text-decoration: none
    cursor: pointer
    &:hover
      color: $secondary
      background: none
      border: 1px solid $secondary
      margin: 0 -1px
    &.selected
      background: $secondary
      border: none
      color: $text-light
      margin: 0
