





























































































































































































@import '~stylus/shared'

@media screen and (min-device-aspect-ratio: 3072/4096)
  #cloud-head
    display none
  #header
    display flex !important
  #back-header
    display none !important
  #main
    padding-top: 68px
    background-image: url('~@/assets/cloudtop.svg')
    background-attachment fixed
    background-size contain
    background-position top center
    background-color $cloud
    height: 100%
    background-repeat: no-repeat

  #cloud-body
    background transparent !important
    padding: 0 70px 30px 70px !important

  #content
    max-width 95%
    margin 0 auto
    margin-top: 50px
    padding 30px
    background white
    padding-top: 50px
    position: relative
    box-shadow: 0 3px 6px rgba(0,0,0,0.16)
  #content-header
    position: absolute;
    background: $secondary;
    color: $text-light;
    left: 0;
    right: 0;
    top: 0;
    height: 48px;
    display: -ms-flexbox;
    display: flex;
    padding-left: 15px
    -ms-flex-direction: row;
    flex-direction: row;
    #back
      font-size: 1.2rem
      padding: 14px 10px 15px 0px;
    #fc
      flex: 1
    a
      color: $text-light
    #logo
      padding: 10px 5px;
      line-height: 30px;
      font-size: 1.2rem;
      letter-spacing: 0.1rem;
  #info-footer
    display: none !important

@media screen and (max-device-aspect-ratio: 3071/4096)
  #header
    display none !important
  #main
    height: 100%;
    background: $body-background;
    background-repeat: no-repeat;
    background-position: top center;
    background-size: contain;
    padding-bottom: 30px
    // background-image:  url('~@/assets/clouds.svg');
  #content-header
    display: none !important
  #info-footer
    display: block !important
  #cloud-body
    background: $body-background

#cloud-head
  position: relative;
  padding-top: 70px;
  background: $secondary;
  img
    width: 100%;
    margin-bottom: -20px;

  #head-content
    position: absolute
    bottom: 15px;
    right: 0;
    color: $secondary;
    font-size: 1.8rem;
    left: 0;
    text-align: center;
    letter-spacing: 0.1rem;

#cloud-body
  padding 0 20px;

#content-body
  padding: 15px 20px;

#back-header
  position: fixed;
  background: $secondary;
  color: $text-light;
  left: 0;
  right: 0;
  top: 0;
  height: 54px;
  padding-top: 4px
  padding-left: 10px
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  z-index: 101
  align-items center
  #back
    font-size: 1.2rem
    padding: 16px 10px 15px 10px;
  #fc
    flex: 1
  a
    color: $text-light
  #logo
    padding: 10px 5px;
    line-height: 32px;
    font-size: 1.2rem;
    letter-spacing: 0.1rem;
  img
      height: 30px;
      margin: 0 10px 0 5px;
  span
    font-size: 1.2rem;
    letter-spacing: 0.1rem;

#info-footer
  padding 30px 10px 10px 10px
  font-size 0.9em
  span
    margin 0 10px
  .idi
    height: 40px;
    display: block;
    margin: 15px auto;
  
  #currently-logged
    font-size: 0.75rem;
    margin-top: 10px;
    font-weight: 500
    font-style italic

    span
      font-weight: bold
      margin: 0 5px;
