#question {
  font-size: 1.2rem;
  white-space: pre-wrap;
}
#question >>> li {
  display: block !important;
}
.date-created {
  font-size: 0.85rem;
  color: #8899a9;
  margin-bottom: 5px;
  font-weight: bold;
}
#session {
  border: 1px solid #eee;
  padding: 10px 10px;
  margin-bottom: 10px;
  margin-top: 10px;
  background: #f7f7f7;
}
.ev-rating {
  margin: 0 0 0 10px;
  padding: 5px 10px;
  color: #fff;
  background: #e7efee;
  border-radius: 5px;
  display: flex;
  font-size: 0.95rem;
  flex-direction: column;
  justify-content: center;
  min-height: 25px;
  font-weight: bold;
  min-width: 25px;
}
img.ev-rating {
  width: 25px;
  height: 25px;
  padding: 5px 5px;
}
#feedback {
  background: #fdfdfd;
  border: 1px solid #eee;
  padding: 10px;
  margin-bottom: 30px;
}
#feedback-data {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 10px auto;
}
#feedback-data #date {
  font-size: 0.85rem;
  color: #8899a9;
  margin-bottom: 5px;
  text-align: left;
}
#feedback-data #rating {
  margin: 0 0 0 10px;
  padding: 10px 10px;
  display: flex;
  flex-direction: row-reverse;
}
#feedback-data #feedback-media {
  margin-top: 10px;
}
video,
audio {
  max-width: 100%;
}
.text-feedback.media {
  font-weight: 500;
  font-size: 1.1rem;
  white-space: pre-wrap;
  padding: 30px;
  border-radius: 5px;
  border: 1px solid #e6e6e6;
  margin: 0 auto;
  text-align: left;
}
#filters {
  display: flex;
  border: 1px solid #eee;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background: #f7f7f7;
}
.filter-row {
  border: 0.25rem solid #f7f7f7;
  border-collapse: collapse;
  min-height: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  margin: 10px;
}
.filter-row select {
  padding: 10px;
}
.filter {
  margin: 0 10px;
  display: flex;
  flex-direction: row;
}
.filter label {
  font-size: 0.85rem;
  line-height: 2rem;
  margin-right: 5px;
  font-weight: bold;
}
.filter input,
.filter select {
  font-size: 0.8rem;
  font-weight: bold;
  padding: 5px 0.5rem;
  border: 1px solid #00a9ce;
  color: #00a9ce;
  background: #fff;
}
#session-filter {
  flex-grow: 1;
}
#session-filter .filter {
  flex-wrap: wrap;
}
#session-filter label,
#session-filter select {
  flex: 1;
}
#feedback-type-filter,
#rating-filter {
  justify-content: flex-start;
  margin-left: 0;
  margin-right: 0;
  border: none;
}
#feedback-type-filter .filter,
#rating-filter .filter {
  flex-wrap: wrap;
  margin-left: 0;
  margin-right: 0;
}
#feedback-type-filter label,
#rating-filter label,
#feedback-type-filter select,
#rating-filter select {
  margin: 0 5px;
}
#feedback-type-filter select,
#rating-filter select {
  min-width: 100px;
}
#time-period-filter {
  justify-content: center;
  flex-wrap: nowrap;
  flex-direction: row-reverse;
}
#time-period-filter .filter {
  margin: 0 5px;
}
#time-period-filter .time-period {
  font-size: 0.8rem;
  font-weight: bold;
  color: #444;
  border: none;
  background: none;
  padding: 5px 10px;
  border-radius: 5px;
  min-height: 32px;
  text-decoration: none;
  cursor: pointer;
}
#time-period-filter .time-period:hover {
  color: #00a9ce;
  background: none;
  border: 1px solid #00a9ce;
  margin: 0 -1px;
}
#time-period-filter .time-period.selected {
  background: #00a9ce;
  border: none;
  color: #fff;
  margin: 0;
}
/*# sourceMappingURL=src/components/AdminMedia.css.map */