@media screen and (min-device-aspect-ratio: 3072/4096) {
  #cloud-head {
    display: none;
  }
  #header {
    display: flex !important;
  }
  #back-header {
    display: none !important;
  }
  #main {
    padding-top: 68px;
    background-image: url("~@/assets/cloudtop.svg");
    background-attachment: fixed;
    background-size: contain;
    background-position: top center;
    background-color: #cae9f7;
    height: 100%;
    background-repeat: no-repeat;
  }
  #cloud-body {
    background: transparent !important;
    padding: 0 70px 30px 70px !important;
  }
  #content {
    max-width: 95%;
    margin: 0 auto;
    margin-top: 50px;
    padding: 30px;
    background: #fff;
    padding-top: 50px;
    position: relative;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  }
  #content-header {
    position: absolute;
    background: #00a9ce;
    color: #fff;
    left: 0;
    right: 0;
    top: 0;
    height: 48px;
    display: -ms-flexbox;
    display: flex;
    padding-left: 15px;
    -ms-flex-direction: row;
    flex-direction: row;
  }
  #content-header #back {
    font-size: 1.2rem;
    padding: 14px 10px 15px 0px;
  }
  #content-header #fc {
    flex: 1;
  }
  #content-header a {
    color: #fff;
  }
  #content-header #logo {
    padding: 10px 5px;
    line-height: 30px;
    font-size: 1.2rem;
    letter-spacing: 0.1rem;
  }
  #info-footer {
    display: none !important;
  }
}
@media screen and (max-device-aspect-ratio: 3071/4096) {
  #header {
    display: none !important;
  }
  #main {
    height: 100%;
    background: #fff;
    background-repeat: no-repeat;
    background-position: top center;
    background-size: contain;
    padding-bottom: 30px;
  }
  #content-header {
    display: none !important;
  }
  #info-footer {
    display: block !important;
  }
  #cloud-body {
    background: #fff;
  }
}
#cloud-head {
  position: relative;
  padding-top: 70px;
  background: #00a9ce;
}
#cloud-head img {
  width: 100%;
  margin-bottom: -20px;
}
#cloud-head #head-content {
  position: absolute;
  bottom: 15px;
  right: 0;
  color: #00a9ce;
  font-size: 1.8rem;
  left: 0;
  text-align: center;
  letter-spacing: 0.1rem;
}
#cloud-body {
  padding: 0 20px;
}
#content-body {
  padding: 15px 20px;
}
#back-header {
  position: fixed;
  background: #00a9ce;
  color: #fff;
  left: 0;
  right: 0;
  top: 0;
  height: 54px;
  padding-top: 4px;
  padding-left: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  z-index: 101;
  align-items: center;
}
#back-header #back {
  font-size: 1.2rem;
  padding: 16px 10px 15px 10px;
}
#back-header #fc {
  flex: 1;
}
#back-header a {
  color: #fff;
}
#back-header #logo {
  padding: 10px 5px;
  line-height: 32px;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
}
#back-header img {
  height: 30px;
  margin: 0 10px 0 5px;
}
#back-header span {
  font-size: 1.2rem;
