






























































































































































































































































@import '~stylus/shared'

#public-charts
  padding-top 60px
  #question
    font-size: 1.8rem
  h3, h2
    margin: 10px 0 10px 0

#description
  padding-top: 10px

.description
  font-size: 1.4rem
#date-created
    font-size: 0.85rem;
    color: $text-lt-gray;
    margin-bottom 5px;
#no-data
  font-size: 1.5rem
  font-weight: bold
  color: #ccc
  text-align: center

#session, #chart
  // background: #fafafa;
  border: 1px solid $border-light;
  padding: 10px;
  // border-radius: 10px;
  margin-bottom 10px
  margin-top 10px

#session-data
  display flex
  flex-direction row
  justify-content space-between
  align-items center

  #date
    font-size: 0.85rem;
    color: $text-lt-gray;
    margin-bottom: 5px;
  #rating
    margin: 0 0 0 10px;
    padding: 10px 10px;
    background: #f4f4f4;
    border-radius: 10px;
    display block;
    width: 25px;
    height: 25px;
h4
  margin-top: 0

#session-media
  margin-top: 10px

#answer-buttons {
  padding: 20px;

  .row {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    a {
      display: block;
      text-decoration: none;
      padding: 30px 20px 15px 20px;
      flex: 0.5;
      font-size: 5rem;
      margin: 5px;
      border-radius: 10px;
    }
  }
}

.event
  display: flex
  flex-direction row
  justify-content space-between
  align-items center
  padding 10px 10px
  margin-bottom: 10px
  background: #fafafa;
  border: 1px solid $border-light;
  border-radius: 10px;
  &:active
    background: $border-light;
  .event-data
    text-align: left;
  .ev-name
    font-size: 1.1rem;
    color: $secondary;
    font-weight: bold;
    margin-bottom: 5px;
  .ev-date
    font-size: 0.85rem;
    color: $text-lt-gray;
    margin-top: 5px;
  .event-icons
    display: flex;
    flex-direction: row-reverse;
  .ev-rating
    margin: 0 0 0 10px;
    padding: 10px 10px;
    background: #f4f4f4;
    // border: 1px solid #ddd;
    border-radius: 10px;
    display block;
    width: 25px;
    height: 25px;
  .ev-media
    margin: 0 0 0 10px;
    padding: 10px 10px;
    background: #f4f4f4;
    border: 1px solid #ddd;
    border-radius: 10px;
    font-size 20px;
    width: 25px;
    height: 25px;
    color: $text-lt-gray;
  a
    width: 100%
    padding 5px
    text-align center

#chart-container
  margin 20px auto
  border 1px solid $border-light

  #donut
    max-width 600px
    margin 0 auto

  #line
    height: 600px

  #chart-title
    text-transform uppercase
    margin 10px 5px 10px 5px
    font-weight: bold
    font-size: 0.9rem

/* Enter and leave animations can use different */
/* durations and timing functions.              */
.slide-enter-active {
  transition: all .3s ease;
}
.slide-leave-active {
  transition: all 0s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-enter, .slide-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateY(5%);
}
