#login-section
  padding: 20px;
  padding-top: 30px;

  .row
    display: flex;
    text-align: center;
    padding: 1px;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex-direction: row;
    flex-direction: row;

    #title
      text-align: center;
      width: 100%;
      font-size: 1.6rem;
      text-transform: uppercase;
      /* font-weight: bold; */
      color: $accent;
      letter-spacing: 0.05em;

    #subtitle
      text-align: center;
      width: 100%;
      font-size: 1.2rem;
      font-weight: lighter;
      text-transform: lowercase;
      letter-spacing: 0.1rem;
    input
      margin: 20px auto 10px auto;
      padding: 10px 1px;
      border: none;
      border-bottom: 1px solid $primary;
      font-size: 1rem;
      color: $text-gray;
      width: 90%;

    button
      font-size: 12pt;
      margin: 0 auto;
      // border-radius: 10px;
      border: none;
      padding: 10px 30px;
      background-color: $text-light;
      background: $secondary ;
      color: $text-light;
      /* text-transform: uppercase; */
      font-weight: bold;
      font-size: 1rem;

    a
      display: block;
      text-decoration: none;
      padding: 30px 20px 15px 20px;
      flex: 0.5;
      font-size: 5rem;
      margin: 5px;
      // border-radius: 10px;

