#event {
  padding: 10px 30px;
}
#event #question {
  text-align: center;
  width: 100%;
  font-size: 1.2rem;
  text-transform: lowercase;
  margin-bottom: 10px;
}
video {
  max-width: 100%;
}
.text-feedback.media {
  font-weight: 500;
  font-size: 1.1rem;
  white-space: pre-wrap;
  padding: 30px;
  border-radius: 30px;
  background: #f2f3f4;
  max-width: 500px;
  margin: 0 auto;
  text-align: left;
}
#status {
  margin: 20px;
  font-style: italic;
}
#feedback,
#chart {
  background: #fafafa;
  border: 1px solid #eee;
  padding: 10px;
  margin-bottom: 10px;
}
#feedback-data {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
#feedback-data #date {
  font-size: 0.85rem;
  color: #8899a9;
  margin-bottom: 5px;
}
#feedback-data #rating {
  margin: 0 0 0 10px;
  padding: 5px 10px;
  background: #f4f4f4;
  border-radius: 10px;
  display: flex;
  font-size: 0.95rem;
  font-weight: bold;
  flex-direction: column;
  justify-content: center;
  min-height: 35px;
  width: 25px;
  color: #fff;
}
#feedback-media {
  margin-top: 10px;
}
#answer-buttons {
  padding: 20px;
}
#answer-buttons .row {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
#answer-buttons .row a {
  display: block;
  text-decoration: none;
  padding: 30px 20px 15px 20px;
  flex: 0.5;
  font-size: 5rem;
  margin: 5px;
  border-radius: 10px;
}
.description {
  padding: 10px;
  font-size: 1.1rem;
  background: #fafafa;
  border: 1px solid #eee;
  margin-bottom: 10px;
}
.description >>> p {
  font-size: 1.1rem;
  margin: 0 0 15px 0;
}
.description >>> p img {
  max-width: 100% !important;
}
/*# sourceMappingURL=src/views/FeedbackDetails.css.map */