























































@import '~stylus/shared'

.heading
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding 0 15px
    box-sizing: border-box
  h3, h4
    flex: 1
    text-align: left
#edit-event
  margin 0 auto
  display: flex
  flex-direction row
  justify-content center
#edit
  // border-radius 10px
  padding 10px 15px
  background $button-primary
  color $text-light
  font-weight: bold
  cursor pointer
  margin: 2px

#event
  padding: 5px 10px
  router-link
    width: 100%
    text-align center
    padding: 15px 30px;
    border-radius: 10px;
    display: block;
    /* background: #E7EFEE; */
    margin: 2px;
    text-decoration: none;
    font-size: 1.1rem;
    &#current
      background: $button-secondary;
      color: $text-light

  a
    text-align center
    padding: 15px 30px;
    // border-radius: 10px;
    display: block;
    /* background: #E7EFEE; */
    margin: 2px;
    text-decoration: none;
    font-size: 1.1rem;
    &#current
      background: $button-secondary;
      color: $text-light

