@media screen and (min-device-aspect-ratio: 3072/4096) {
  #header-container {
    display: block !important;
  }
  #main {
    padding-top: 68px;
  }
}
#header-container {
  display: none;
}
#microphone-icon {
  font-size: 20vh;
  color: #bbb;
  padding: 20px;
}
video {
  max-width: 100%;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}
#text-field textarea {
  width: 78%;
  min-height: 300px;
  border: 1px solid #ccc;
  resize: vertical;
  font-size: 1.2rem;
  padding: 10px;
}
#broadcast {
  display: block;
  margin: 0 auto;
}
#playback {
  margin-top: 20px;
  margin-bottom: 20px;
}
#playback-controls {
  padding: 10px 20px;
  background: #fff;
}
#playback-controls #consent-container {
  width: 80%;
  margin: 0 auto;
}
#playback-controls #consent-container label {
  margin: 10px 0;
  display: block;
  text-align: left;
}
#playback-controls #consent-container label span {
  margin-left: 10px;
}
#playback-controls #pb-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
#playback-controls #pb-container>button {
  min-width: 150px;
  margin: 0 5px 10px 5px;
}
#playback-controls #upload {
  display: block;
  margin: 10px auto;
  font-size: 1.2rem;
  padding: 15px 30px;
  background: #92c020;
  color: #fff;
  font-weight: 500;
  width: 100%;
  border: none;
  flex: 1;
  max-width: 300px;
}
#playback-controls #upload:disabled {
  background: #777;
  color: #555;
}
#playback-controls #restart {
  display: block;
  margin: 10px auto;
  font-size: 1.2rem;
  padding: 15px 30px;
  border-radius: 10px;
  background: #e7efee;
  color: #444;
  width: 100%;
  border: none;
  flex: 1;
  max-width: 300px;
}
#playback-controls #skip {
  width: 100%;
}
#none {
  padding: 10px 30px;
  background: #e7efee;
  border: 1px solid #eee;
  margin: 10px auto;
  text-align: center;
  color: #92c020;
  max-width: 300px;
  box-sizing: border-box;
}
#none a {
  color: #92c020;
  font-size: 1.3rem;
  display: block;
}
#answer-buttons {
  padding: 20px;
}
#answer-buttons .row {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
#answer-buttons .row a {
  display: block;
  text-decoration: none;
  padding: 30px 20px 15px 20px;
  flex: 0.5;
  font-size: 5rem;
  margin: 5px;
  border-radius: 10px;
}
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}
/* Hide default HTML checkbox */
.switch input {
  display: none;
}
/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #f44336;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 34px;
  margin: 0 !important;
}
.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: #fff;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 50%;
}
input:checked + .slider {
  background-color: #4caf50;
}
input:focus + .slider {
  box-shadow: 0 0 1px #4caf50;
}
input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
.toggle-row {
  text-align: left;
  margin: 20px 0;
}
.toggle-element {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.toggle-element .answer.public {
  margin: 5px;
  min-width: 50px;
  text-align: center;
  font-size: 1rem;
  font-weight: bold;
  color: #778594;
}
.toggle-element .answer.public.selected {
  color: #2c3e50;
}
