@media screen and (min-device-aspect-ratio: 3072/4096) {
  #answer-buttons .likert-10 {
    flex-direction: column;
  }
  #answer-buttons .likert-row {
    flex-direction: row;
    width: 100%;
    padding: 10px 10px;
    font-weight: normal;
    font-size: 1.2rem;
  }
  #answer-buttons label {
    margin: 0 10px 0 10px;
    padding: 0;
    text-align: left;
  }
  #answer-buttons input {
    margin: 0 10px 0 10px;
    width: auto;
  }
  #intro-view {
    background: #fff !important;
  }
}
@media screen and (max-device-aspect-ratio: 3071/4096) {
  #answer-buttons .likert-10 {
    flex-direction: column;
  }
  #answer-buttons .likert-row {
    flex-direction: row;
    width: 100%;
    padding: 10px 10px;
    font-weight: normal;
    font-size: 1.2rem;
  }
  #answer-buttons label {
    margin: 0 10px 0 10px;
    padding: 0;
    text-align: left;
  }
  #answer-buttons input {
    margin: 0 10px 0 10px;
    width: auto;
  }
}
#followup-options {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}
#title {
  text-align: center;
  width: 100%;
  font-size: 1.6rem;
  text-transform: uppercase;
  color: #bbd37a;
  letter-spacing: 0.05em;
}
#video-picker {
  position: absolute;
  top: -200px;
}
#answer-buttons {
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
#answer-buttons #instructions {
  text-align: center;
  margin-top: 10px;
  font-size: 0.9em;
  color: #777;
}
#answer-buttons .likert-container {
  display: flex;
  flex-direction: row;
  box-shadow: 1px 1px 9px 1px #dadada;
  background: #fff;
}
#answer-buttons .labels {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 20px;
  box-sizing: content-box;
  font-size: 1rem;
  font-weight: 500;
  max-width: 160px;
}
#answer-buttons .likert-10 {
  padding: 0 0px;
  text-align: left;
  display: flex;
  min-width: 100px;
}
#answer-buttons .likert-10 .likert-row {
  display: flex;
  align-items: center;
  border: 1px solid #eee;
  box-sizing: border-box;
  color: #2c3e50;
}
#answer-buttons .likert-10 .likert-row label {
  flex: 1;
}
#answer-buttons .likert-10 .likert-row.selected {
  background: #92c020;
  color: #fff;
  font-weight: bold;
}
#answer-buttons .row {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  flex: 1;
  min-width: 280px;
  justify-content: space-around;
}
#answer-buttons .row a {
  display: block;
  text-decoration: none;
  flex: 1;
  padding: 30px 30px;
  -ms-flex: 0.5;
  flex: 0.5;
/* font-size: 5rem; */
  max-width: 30%;
  margin: 10px;
  border-radius: 10px;
  border: 0;
  background: #f7f7f7;
  box-shadow: 1px 1px 9px 1px #dadada;
}
#answer-buttons .row .selected {
  background: currentColor;
}
#follow-up {
  padding: 10px 30px;
}
#follow-up #video,
#follow-up #audio,
#follow-up #text {
  padding: 30px;
  background: #92c020;
  border: 1px solid #bbd37a;
  margin: 10px;
  font-size: 2rem;
  text-align: center;
  color: #fff;
  flex: 1;
  min-width: 120px;
}
#follow-up #video a,
#follow-up #audio a,
#follow-up #text a {
  margin-top: 10px;
  color: #fff;
  font-size: 1.3rem;
  display: block;
}
#follow-up #none {
  padding: 20px 30px;
  background: #e7efee;
  border: 1px solid #eee;
  margin: 10px;
  text-align: center;
  color: #92c020;
}
#follow-up #none a {
  color: #92c020;
  font-size: 1.3rem;
  display: block;
}
.submit,
.proceed {
  padding: 10px 30px;
}
.submit button,
.proceed button {
  display: block;
  margin: 10px auto;
  font-size: 1.2rem;
  padding: 15px 30px;
  background: #92c020;
  color: #fff;
  width: 100%;
  border: none;
  font-weight: 500;
  box-shadow: 1px 1px 9px 1px #dadada;
}
.submit .disabled,
.proceed .disabled {
  background: #d3d3d3;
}
#intro-view,
#question-view {
  background: #cae9f7;
  background: linear-gradient(180deg, #cae9f7, #fff);
  padding: 0 0 10px 0;
}
#question-view h3 {
  margin: 0;
  padding: 15px;
}
.description {
  padding: 10px 20px;
  font-size: 1.1rem;
}
.description >>> p {
  font-size: 1.1rem;
  margin: 0 0 15px 0;
}
.description >>> p img {
  max-width: 100% !important;
}
