











































































@import '~stylus/shared'

@media screen and (min-device-aspect-ratio: 3072/4096)
  #header
    #link-logo
      display flex !important

    #logo
      display none !important

    #nav-links
      display flex !important

#header
  position: fixed;
  background: $secondary;
  // border-bottom: 5px solid #9dc3cb;
  color: $text-light;
  left: 0;
  right: 0;
  top: 0;
  height: 64px;
  padding-top: 4px
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  z-index: 200
  padding-left: 18px
  justify-content space-between
  align-items center

  #logo-container
    display flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  #back
    font-size: 1.2rem
    padding: 16px 10px 15px 2px;

  a
    color: $text-light
  #logo
    padding: 10px 5px;
    line-height: 32px;
    font-size: 1.2rem;
    letter-spacing: 0.1rem;
    display flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    img
      height: 30px;
      margin-right: 10px;
  #link-logo
    padding: 10px 5px;
    line-height: 32px;
    font-size: 1.2rem;
    letter-spacing: 0.1rem;
    display none
    flex-direction: row;
    justify-content: center;
    align-items: center;
    img
      height: 30px;
      margin-right: 10px;
    a
      color: $text-light
      text-decoration : none
  #nav-links
    display none
    flex-direction row
    padding-right: 30px
    #fc
      flex: 1
      padding 5px
      margin 0 10px
