.row {
  display: flex;
  text-align: center;
  padding: 1px;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-direction: row;
  flex-direction: row;
}
.row #title {
  text-align: center;
  width: 100%;
  font-size: 1.6rem;
  text-transform: uppercase;
/* font-weight: bold; */
  color: #bbd37a;
  letter-spacing: 0.05em;
}
.row #subtitle {
  text-align: center;
  width: 100%;
  font-size: 1rem;
  font-weight: lighter;
  text-transform: lowercase;
  letter-spacing: 0.1rem;
  margin-bottom: 10px;
}
.row input {
  margin: 20px auto 10px auto;
  padding: 10px 1px;
  border: none;
  border-bottom: 1px solid #92c020;
  font-size: 1rem;
  color: #444;
  width: 90%;
}
.row button {
  font-size: 12pt;
  margin: 0 auto;
  border-radius: 10px;
  border: none;
  padding: 10px 30px;
  background-color: #fff;
  background: #00a9ce;
  color: #fff;
/* text-transform: uppercase; */
  font-weight: bold;
  font-size: 1rem;
}
.row a {
  display: block;
  text-decoration: none;
  padding: 30px 20px 15px 20px;
  flex: 0.5;
  font-size: 5rem;
  margin: 5px;
  border-radius: 10px;
}
#event {
  padding: 5px 10px;
}
#event router-link {
  width: 100%;
  text-align: center;
  padding: 15px 30px;
  display: block;
/* background: #E7EFEE; */
  margin: 2px;
  text-decoration: none;
  font-size: 1.1rem;
}
#event router-link#current {
  background: #00a9ce;
  color: #fff;
}
#event router-link#past {
  background: #e7efee;
  color: #444;
}
#event a {
  text-align: center;
  padding: 15px 30px;
  display: block;
/* background: #E7EFEE; */
  margin: 2px;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
}
#event a#current {
  background: #00a9ce;
  color: #fff;
}
#event a#past {
  background: #e7efee;
  color: #444;
}
