








































































































































@import '~stylus/shared'

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: $text-dark;
  padding-bottom: 60px;
  flex: 1;
}

.fade-enter-active,
.fade-leave-active {
  transition-duration: 0.1s;
  transition-property: opacity;
  transition-timing-function: ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0
}

h4 {
  margin-top: 0
}

#main {
  // padding-top: 68px;
}

a {
  color: $link-normal;
}
a:hover {
  color: $link-hover;
}

video, audio {
  max-width: 100%
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}
