@media screen and (min-device-aspect-ratio: 3072/4096) {
  #header #link-logo {
    display: flex !important;
  }
  #header #logo {
    display: none !important;
  }
  #header #nav-links {
    display: flex !important;
  }
}
#header {
  position: fixed;
  background: #00a9ce;
  color: #fff;
  left: 0;
  right: 0;
  top: 0;
  height: 64px;
  padding-top: 4px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  z-index: 200;
  padding-left: 18px;
  justify-content: space-between;
  align-items: center;
}
#header #logo-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
#header #back {
  font-size: 1.2rem;
  padding: 16px 10px 15px 2px;
}
#header a {
  color: #fff;
}
#header #logo {
  padding: 10px 5px;
  line-height: 32px;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
#header #logo img {
  height: 30px;
  margin-right: 10px;
}
#header #link-logo {
  padding: 10px 5px;
  line-height: 32px;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
  display: none;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
#header #link-logo img {
  height: 30px;
  margin-right: 10px;
}
#header #link-logo a {
  color: #fff;
  text-decoration: none;
}
#header #nav-links {
  display: none;
  flex-direction: row;
  padding-right: 30px;
}
#header #nav-links #fc {
  flex: 1;
  padding: 5px;
  margin: 0 10px;
}
