#date-created {
  font-size: 0.85rem;
  color: #8899a9;
  margin-bottom: 5px;
}
.ev-name {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.session-edit {
  color: #00a9ce;
  margin-left: 10px;
  text-decoration: none;
  cursor: pointer;
  width: auto !important;
}
.heading {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
h3,
h4 {
  flex: 1;
  text-align: left;
}
.optional {
  font-size: 0.8em;
  color: #444;
  margin: 0 5px;
}
#edit-event {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
#edit {
  padding: 10px 15px;
  background: #00a9ce;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  margin: 2px;
}
#discard {
  padding: 10px 15px;
  background: #bbb;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  margin: 2px;
}
#save {
  padding: 10px 15px;
  background: #92c020;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  display: inline-block;
  margin: 2px;
}
#event-settings {
  text-align: left;
}
#event-settings .section {
  padding: 10px;
  border: 1px solid #eee;
  margin-bottom: 10px;
}
#event-settings h4 {
  text-align: center;
  margin-bottom: 0px;
}
.row {
  padding: 10px 0;
}
label {
  color: #00a9ce;
  font-size: 0.9rem;
  font-weight: bold;
  display: block;
}
.label-for {
  display: inline-block !important;
}
.event-data {
  padding: 5px 10px;
  font-size: 1rem;
  display: block;
  width: 100%;
}
.no-data {
  font-weight: bold;
  font-style: italic;
  font-size: 0.9rem;
  color: #bbb;
}
input[type=text].event-input {
  padding: 10px;
  font-size: 0.9rem;
  min-width: 50%;
  border: 1px solid #bbb;
  width: 100%;
  box-sizing: border-box;
}
select.event-input {
  padding: 10px;
  font-size: 0.9rem;
  min-width: 50%;
}
#add-session {
  text-align: left;
}
#session {
  border: 1px solid #eee;
  padding: 10px;
  margin-bottom: 10px;
  margin-top: 10px;
}
.event {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px 10px;
  margin-bottom: 10px;
  background: #fafafa;
  border: 1px solid #eee;
}
.event:active {
  background: #bbb;
}
.event .event-data {
  text-align: left;
}
.event .ev-name {
  font-size: 1.1rem;
  color: #00a9ce;
  font-weight: bold;
  margin-bottom: 5px;
}
.event .ev-date {
  font-size: 0.85rem;
  color: #8899a9;
  margin-top: 5px;
}
.event a {
  width: 100%;
  padding: 5px;
  text-align: center;
}
.quill-data >>> img {
  max-width: 100%;
}
