#login-section {
  padding: 20px;
  padding-top: 30px;
}
#login-section .row {
  display: flex;
  text-align: center;
  padding: 1px;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-direction: row;
  flex-direction: row;
}
#login-section .row #title {
  text-align: center;
  width: 100%;
  font-size: 1.6rem;
  text-transform: uppercase;
/* font-weight: bold; */
  color: #bbd37a;
  letter-spacing: 0.05em;
}
#login-section .row #subtitle {
  text-align: center;
  width: 100%;
  font-size: 1.2rem;
  font-weight: lighter;
  text-transform: lowercase;
  letter-spacing: 0.1rem;
}
#login-section .row input {
  margin: 20px auto 10px auto;
  padding: 10px 1px;
  border: none;
  border-bottom: 1px solid #92c020;
  font-size: 1rem;
  color: #444;
  width: 90%;
}
#login-section .row button {
  font-size: 12pt;
  margin: 0 auto;
  border: none;
  padding: 10px 30px;
  background-color: #fff;
  background: #00a9ce;
  color: #fff;
/* text-transform: uppercase; */
  font-weight: bold;
  font-size: 1rem;
}
#login-section .row a {
  display: block;
  text-decoration: none;
  padding: 30px 20px 15px 20px;
  flex: 0.5;
  font-size: 5rem;
  margin: 5px;
}
