@media screen and (min-device-aspect-ratio: 3072/4096)

  #answer-buttons
    // flex-wrap wrap

    .likert-10
      flex-direction: column;

    .likert-row
      flex-direction: row;
      width: 100%;
      padding: 10px 10px;
      font-weight: normal;
      font-size: 1.2rem;

    label
      margin: 0 10px 0 10px;
      padding: 0;
      text-align: left;

    input
      margin: 0 10px 0 10px;
      width: auto;

  #intro-view
    background: $body-background !important;

@media screen and (max-device-aspect-ratio: 3071/4096)
  #answer-buttons
    // flex-wrap wrap

    .likert-10
      flex-direction: column;

    .likert-row
      flex-direction: row;
      width: 100%;
      padding: 10px 10px;
      font-weight: normal;
      font-size: 1.2rem;

    label
      margin: 0 10px 0 10px;
      padding: 0;
      text-align: left;

    input
      margin: 0 10px 0 10px;
      width: auto;

#main {
  // padding-top: 68px;
}

#followup-options
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;

#title
  text-align: center;
  width: 100%;
  font-size: 1.6rem;
  text-transform: uppercase;
  color: $accent;
  letter-spacing: 0.05em;

#video-picker
  position absolute
  top: -200px

#answer-buttons {
  padding: 20px;
  display: flex;
  flex-direction row;
  justify-content: center;

  #instructions {
    text-align center
    margin-top 10px
    font-size 0.9em
    color: #777
  }

  .likert-container {
    display: flex;
    flex-direction: row;
    box-shadow: 1px 1px 9px 1px #dadada
    background: $body-background
  }

  .labels {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px 20px;
    box-sizing: content-box;
    font-size: 1rem;
    font-weight: 500;
    max-width: 160px;
  }

  .likert-10 {
    padding: 0 0px;
    text-align: left;
    // box-shadow: 1px 1px 9px 1px #dadada
    display: flex;
    min-width: 100px;

      .likert-row {
        display: flex;
        align-items: center;
        border: 1px solid $border-light;
        box-sizing: border-box;
        color: $text-dark;

        label {
          flex: 1;
        }

        &.selected {
          background: $button-primary;
          color: $text-light;
          font-weight: bold;
        }
      }
  }

  .row {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    flex: 1
    min-width: 280px
    justify-content: space-around;

    a {
      display: block;
    text-decoration: none;
    flex 1;
    padding: 30px 30px;
    -ms-flex: 0.5;
    flex: 0.5;
    /* font-size: 5rem; */
    max-width: 30%
    margin: 10px;
    border-radius: 10px;
    border: 0;
    background: #F7F7F7;
    box-shadow: 1px 1px 9px 1px #dadada
    }

    .selected {
      background: currentColor
    }
  }
}

#follow-up
  padding 10px 30px
  #video, #audio, #text
    padding: 30px;
    background: $button-primary;
    border: 1px solid $border-primary;
    margin: 10px;
    // border-radius: 10px;
    font-size: 2rem;
    text-align: center;
    color: $text-light;
    flex: 1;
    min-width: 120px;
    a
      margin-top: 10px
      color: $text-light;
      font-size: 1.3rem;
      display: block

  #none
    padding: 20px 30px;
    background: $button-lt-gray;
    border: 1px solid $border-light ;
    margin: 10px;
    // border-radius: 10px;
    text-align: center;
    color: $button-primary;
    a
      color: $button-primary;
      font-size: 1.3rem;
      display: block

.submit, .proceed
  padding: 10px 30px;

  button
    display: block;
    margin: 10px auto;
    font-size: 1.2rem;
    padding: 15px 30px;
    // border-radius: 10px;
    background: $button-primary;
    color: $text-light;
    width: 100%;
    border: none;
    font-weight: 500
    box-shadow: 1px 1px 9px 1px #dadada;

  .disabled
    background: lightgray

#intro-view, #question-view
  background: #cae9f7;
  background: linear-gradient(180deg, $cloud, $body-background);
  padding: 0 0 10px 0;

#question-view
  h3
    margin: 0
    padding: 15px;

.description
  padding: 10px 20px;
  font-size: 1.1rem;
  // background: #cae9f7;
  // background: linear-gradient(180deg, #cae9f7, #fff);

.description >>> p
  font-size: 1.1rem;
  margin: 0 0 15px 0;
  img
    max-width: 100% !important

