








































































@import '~stylus/shared'
  
#main {
  height: 100%;
  // background: white;
  background-repeat: no-repeat;
  background-position: top center;
  background-size: contain;
  // background-image:  url('~@/assets/clouds.svg');
}

#cloud-head
  position: relative;
  padding-top: 70px;
  background: #4192A1;
  img
    width: 100%;
    margin-bottom: -20px;

  #head-content
    position: absolute
    bottom: 15px;
    right: 0;
    color: $secondary;
    font-size: 2.2rem;
    left: 0;
    text-align: center;
    letter-spacing: 0.1rem;

#cloud-body
  background: white
  padding 0 20px;

#answer-buttons
  padding: 10px 20px;

#events-view
  padding: 10px 20px;

  .button
    display: block;
    margin: 10px auto;
    font-size: 1.2rem;
    padding: 15px 30px;
    // border-radius: 10px;
    background: $button-primary;
    color: $text-light;
    border: none;
    font-weight: 500

  #signout
    display: block;
    margin: 10px auto;
    font-size: 1.2rem;
    padding: 15px 30px;
    // border-radius: 10px;
    background: $button-lt-gray;
    color: $text-gray;
    font-weight: 500
    border: none;
